import axios from "axios";
import { setAlert } from "./alert";
import {
    GET_POSTS,
    POST_ERROR,
    UPDATE_LIKES,
    DELETE_POST,
    ADD_POST,
    GET_POST,
    ADD_COMMENT,
    REMOVE_COMMENT,
} from "./types";

//Get posts
export const getPosts = () => async (dispatch) => {
    await axios
        .get("/api/posts")
        .then((res) => {
            dispatch({
                type: GET_POSTS,
                payload: res.data,
            });
        })
        .catch((err) => {
            dispatch({
                type: POST_ERROR,
                payload: {
                    msg: err.response.statusText,
                    status: err.response.status,
                },
            });
        });
};

//Get posts
export const getPost = (id) => async (dispatch) => {
    await axios
        .get(`/api/posts/${id}`)
        .then((res) => {
            dispatch({
                type: GET_POST,
                payload: res.data,
            });
        })
        .catch((err) => {
            dispatch({
                type: POST_ERROR,
                payload: {
                    msg: err.response.statusText,
                    status: err.response.status,
                },
            });
        });
};

//Toggle Like
export const addLike = (id) => async (dispatch) => {
    await axios
        .put(`/api/posts/like/${id}`)
        .then((res) => {
            dispatch({
                type: UPDATE_LIKES,
                payload: { id, likes: res.data },
            });
        })
        .catch((err) => {
            dispatch({
                type: POST_ERROR,
                payload: {
                    msg: err.response.statusText,
                    status: err.response.status,
                },
            });
        });
};

//Delete Post
export const deletePost = (id) => async (dispatch) => {
    await axios
        .delete(`/api/posts/${id}`)
        .then((res) => {
            dispatch({
                type: DELETE_POST,
                payload: id,
            });

            dispatch(setAlert("Post Removed", "success"));
        })
        .catch((err) => {
            dispatch({
                type: POST_ERROR,
                payload: {
                    msg: err.response.statusText,
                    status: err.response.status,
                },
            });
        });
};

//Add Post
export const addPost = (formData) => async (dispatch) => {
    const config = {
        headers: {
            "Content-Type": "application/json",
        },
    };
    await axios
        .post(`/api/posts`, formData, config)
        .then((res) => {
            dispatch({
                type: ADD_POST,
                payload: res.data,
            });

            dispatch(setAlert("Post Created", "success"));
        })
        .catch((err) => {
            dispatch({
                type: POST_ERROR,
                payload: {
                    msg: err.response.statusText,
                    status: err.response.status,
                },
            });
        });
};

