//Delete Post
export const deleteComment = (postId, commentId) => async (dispatch) => {
    await axios
        .delete(`/api/posts/comment/${postId}/${commentId}`)
        .then((res) => {
            dispatch({
                type: REMOVE_COMMENT,
                payload: commentId,
            });

            dispatch(setAlert("Comment Removed", "success"));
        })
        .catch((err) => {
            dispatch({
                type: POST_ERROR,
                payload: {
                    msg: err.response.statusText,
                    status: err.response.status,
                },
            });
        });
};
